@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
/* var */
:root {
  --primary-white: #fff;
}

body {
  font-family: "Lato", sans-serif;
}

/* NATIVE */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo {
  width: 4rem;
}

nav {
  z-index: 2;
}
.nav-link {
  cursor: pointer;
  font-weight: 400;
  letter-spacing: 0.7px;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin: 3px 5px;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus,
.navbar-nav > li > a:hover {
  border-top: 2px solid #fff;
  margin: 1px 5px;
}

.header-wrapper {
  position: relative;
  background: url(https://images.unsplash.com/photo-1580927752452-89d86da3fa0a?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  color: #fff;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.main-info h1 {
  color: #fff;
  text-transform: uppercase;
}

.typed-text {
  font-size: 2rem;
}

.btn-hero {
  z-index: 1;
}
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem;
  }
  .typed-text {
    font-size: 1.3rem;
  }
  .tsparticles-canvas-el {
    display: none;
  }
}

#about {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: #fff;
  -webkit-align-items: center;
          align-items: center;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
  padding: 5rem 0px;
}

#about:hover {
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}

img.photo {
  object-fit: cover;
  object-position: center;
  height: 23vh;
  width: 23vh;
  border-radius: 50%;
}

.typed-text-about {
  font-size: 1rem;
}

@media (max-width: 768px) {
  #about a.btn-hero {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    padding: 10px 0px;
  }
}

#projects .card-project {
  border-radius: 5px;
  border: 3px solid #f5f5f5;
}

#projects .card-project:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  border-radius: 5px;
  border: 3px solid #007bff;
}

#projects img {
  height: 20vh;
  object-fit: cover;
}

#projects .card-project ul > li {
  font-size: small;
  color: #007bff;
}

.btn-hire {
  text-transform: uppercase;
  text-align: center;
}

p.SRLCaptionText {
  color: black !important;
}

